.header-overlay {
  position: fixed;
//   display: none;
  top: 0;
  left: 0;
  z-index: 30;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.53);
  opacity: 0;
  transition: all 0.3s ease-out;
  visibility: hidden;
}
.navbar-box__local {
  // margin-top: 100px;
  top: 98px;
  height: 40px;
  display: flex !important;
  align-items: center;
  padding: 0;
  background: #fff;
  position: fixed;
  width: 100%;
  color: #444;
  z-index: 50;
  svg {
    margin-right: 5px;
  }
  .dropdown-toggle {
    position: relative;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 10px;
    p {
      font-size: 13px;
    }
    .arrow {
      position: absolute;
      right: 3px;
      top: 34%;
      font-weight: normal;
    }
  }
}
.box-sliding {
  margin-top: 79px;
  margin-bottom: 15px;
  &__left {
    width: 20%;
    padding: 0 15px;
    padding-left: 0;
    height: 376px;
    .box-list-menu {
      margin-block-start: 0;
      margin-block-end: 0;
      height: 375.5px;
      // padding: 0 10px;
      padding-inline-start: 0;
      list-style: none;
      border-radius: 2px;
      background: #fff;
      box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
      position: relative;
      .menu-item {
        cursor: pointer;
        padding: 0 10px;
        a {
          min-height: 31.3px;
          display: flex;
          align-items: center;
          background: 0 0;
          font-size: 12px;
          font-weight: 700;
          text-decoration: none !important;
          color: #343a40;
          transition: 0.3s;
          svg {
            margin-right: 5px;
            // font-size: 18px;
            width: 20px;
            height: 20px;
          }
        }
        .arrow-right {
          position: absolute;
          right: 5px;
          svg {
            width: 10px;
            height: 11px;
          }
        }
        .box-list-menu.box-menu__child {
          width: 220px;
          height: auto;
          min-height: 375px;
          visibility: hidden;
          box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
          position: absolute;
          top: 0;
          right: -218px;
          z-index: 100;
          opacity: 0;
        }
        .box-list-menu.box-menu__child > .menu-item > a {
          font-size: 12px;
          color: #666;
        }
        &:hover {
          border-radius: 2px;
          background-color: #f8f8f8;
        }
        .box-list-menu {
          .box-menu__child-title {
            cursor: default;
            margin: 0;
            padding: 10px 5px;
            color: #666;
            font-size: 12px;
          }
        }
      }
      .menu-item:hover > .box-list-menu {
        visibility: visible;
        opacity: 1;
      }
    }
  }
  &__center {
    width: 55%;
    // padding: 0 15px;
    height: 376px;
    position: relative;
    @media screen and (max-width: 768px) {
      width: 100%;
      padding: 0;
      height: auto;
      margin-top: 68px;
    }
    .swiper-container {
      @media screen and (max-width: 768px) {
        padding-bottom: 10px;
      }
      .swiper-wrapper {
        .swiper-slide {
            cursor: pointer;
          img {
            border-top-left-radius: 2px;
            border-top-right-radius: 2px;
          }
        }
      }
    }
    &:hover {
      .swiper-container {
        .swiper-button-prev {
          left: -30px !important;
        }
        .swiper-button-next {
          right: -30px !important;
        }
      }
    }
  }
  &__right {
    width: 25%;
    height: 376px;
    padding: 0 15px;
    padding-right: 0;
    .right__box-img {
      width: 100%;
      margin-bottom: 15px;
      height: 115px;
      img {
        width: 100%;
        height: 100%;
        border-radius: 2px;
      }
    }
  }
}
.banner {
  img {
    width: 100%;
    height: auto;
  }
}
.box-menu {
  margin: 0 5px;
  .shadow-sm {
    box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
    width: 100%;
    border-radius: 4px;
    background: #fff;
    margin-top: 15px;
    padding: 5px 15px;
    .list-menu {
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 0;
      padding-left: 0;
      list-style: none;
      margin-top: 0;
      li {
        width: calc(100% / 5);
        margin-bottom: 5px;
        transition: 0.3s;
      }
      .box-icon {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 5px;
        font-size: 20px;
        color: #666;
      }
      .box-name {
        padding: 0 2px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1.4;
        text-align: center;
        text-transform: capitalize;
        color: #666;
      }
    }
  }
}
.swiper-button-prev {
  color: #fff !important;
  width: 60px !important;
  height: 60px !important;
  background: rgba(0, 0, 0, 0.3);
  top: 48% !important;
  left: -60px !important;
  transition: 0.3s;
  outline: none;
  border-radius: 50%;
  &::after {
    padding-left: 20px;
    font-size: 16px !important;
    font-weight: 700 !important;
  }
}
.swiper-button-next {
  color: #fff !important;
  width: 60px !important;
  height: 60px !important;
  background: rgba(0, 0, 0, 0.3);
  top: 48% !important;
  right: -60px !important;
  transition: 0.3s;
  outline: none;
  border-radius: 50%;
  &::after {
    padding-right: 20px;
    font-size: 16px !important;
    font-weight: 700 !important;
  }
}
.swiper-button-prev-custom {
  position: absolute;
  top: 40%;
  z-index: 2;
}
.slider_menu {
  height: 72px;
  border-top: 1px solid #f4f4f4;
  background: #fff;
  position: unset;
  .swiper-slide {
    cursor: pointer;
    // width: 140px !important;
    // margin-right: 0 !important;
    width: 132px !important;
    margin-right: 0 !important;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 2px;
    border-radius: 0;
    background: #fff;
    font-size: 12px;
    line-height: 20px;
    text-align: center;
    color: #343a40;
    overflow: hidden;
    cursor: pointer;
  }
}
.shadow-sm {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075) !important;
}
.box-sliding__center .slider_menu .swiper-slide.swiper-slide-active {
  border-bottom: 2px solid #d70018 !important;
  font-weight: 700;
  color: #343a40;
}
.slider-sp {
  .swiper-slide {
    img {
      width: 100%;
      height: auto;
    }
  }
  .swiper-pagination {
    .swiper-pagination-bullet {
      width: calc(100% / 12);
      height: 3px;
      border-radius: 0 !important;
      margin: 0 5px !important;
    }
    .swiper-pagination-bullet-active {
      background: #d70018;
    }
  }
}
.slider-sp.swiper-container-horizontal > .swiper-pagination-bullets {
  bottom: 2px;
}
.location_sp {
  position: relative;
  .dropdown-menu {
    // display: none;
    position: absolute;
    padding: 0;
    border: 0;
    overflow: hidden;
    top: 50px;
    width: calc(100% - 10px);
    margin-right: 5px;
    margin-left: 5px;
    left: 0;
    z-index: 1000;
    min-width: 160px;
    font-size: 14px;
    text-align: left;
    list-style: none;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 6px 12px rgb(0 0 0 / 18%);
    transition: all 0.3s;
    visibility: hidden;
    opacity: 0;
    & > li > a {
      color: #333;
      display: block;
      padding: 5px 20px;
      font-weight: 400;
      line-height: 1.42857143;
      white-space: nowrap;
      svg {
        margin-right: 5px;
      }
      &:hover {
        color: #262626;
        text-decoration: none;
        background-color: #f5f5f5;
      }
    }
  }
}
.box-menu__click {
    // display: none;
    z-index: 100;
    top: 83px;
    position: absolute;
    width: 100%;
    border-radius: 0;
    margin-left: 0;
    height: 100%;
    background: rgba(0, 0, 0, 0.53);
    visibility: hidden;
    opacity: 0;
    transition: all 0.3s;
    .box-menu__list-menu {
        padding: 0;
        border-radius: 0;
    }
    &.active {
        visibility: unset;
        opacity: 1;
    }
}